.start {
    width: 100%;
    height: 90vh;
    overflow: hidden;
    &__title-top {
        width: 100%;
        padding: 0 percentage(73/1400);
        font-family: $kizo;
        font-size: 86px;
        color: $coral;
        text-transform: uppercase;
        display: block;
        z-index: 1; }
    &__title-top_left {
        text-align: left; }
    &__title-top_right {
        text-align: right; }
    &__title-btm {
        position: absolute;
        z-index: 1; }
    &__title-btm_left {
        font-family: $journalism;
        font-size: 93px;
        color: #000;
        line-height: 1;
        text-transform: uppercase; }
    &__title-btm_right {
        font-family: $sitka;
        font-style: italic;
        font-weight: 600;
        font-size: 54px;
        color: #fff;
        line-height: 1; }
    &__terms {
        width: 100%;
        @include counter(1, ''); }
    &__terms-item {
        width: 100%;
        @include clr;
        &:before {
            font-family: $journalism;
            font-size: 170px;
            line-height: 0.85;
            text-transform: uppercase;
            width: 25%;
            float: left; } }
    &__text {
        font-family: $journalism;
        font-size: 34px;
        line-height: 46.52/41;
        text-transform: uppercase;
        width: 65%;
        float: left; }
    &__prizes {
        position: absolute;
        top: get-vh(130px); }
    &__prize {
        height: 160px; }
    &__switcher {
        position: absolute;
        bottom: get-vh(165px);
        z-index: 1;
        display: table;
        cursor: pointer; }
    &__icon-arrow {
        display: table-cell;
        vertical-align: middle; }
    &__icon-heart {
        display: inline-block;
        margin: 0 6px; } }
