.start-right {
    width: 100%;
    height: 90vh;
    position: relative;
    display: inline-block;
    @include image('bg-curly-start-right.jpg');
    @include cover;
    @include clr;
    &__title-btm {
        top: get-vh(300px);
        right: percentage(73/1400); }
    &__title-btm_right {
        font-size: 54px;
        color: #fff; }
    &__terms {
        max-width: 1400px;
        margin: 0 auto;
        position: absolute;
        bottom: get-vh(70px);
        left: 50%;
        transform: translate(-50%); }
    &__terms-item {
        float: left;
        width: 33%;
        &:before {
            font-family: $sitka;
            font-style: italic;
            font-weight: 600;
            font-size: 186px;
            color: #fff;
            line-height: 1;
            text-transform: lowercase; } }
    &__text {
        font-family: $sitka;
        font-style: italic;
        font-weight: 600;
        font-size: 34px;
        color: #fff;
        line-height: 46.52/34;
        text-transform: lowercase; }
    &__prizes {
        left: percentage(73/1400); }
    &__block {
        display: inline-block;
        width: 360px;
        height: 100px;
        &:before {
            background-color: #000;
            z-index: 0; }
        &:after {
            width: 360px;
            top: 15px;
            left: 15px; } }
    &__block-frame {
        position: relative;
        &:after {
            border-color: $bright-pink; } }
    &__switcher {
        left: 0; } }



