
body {
    background: #fff;
    font-family: $sitka;
    font-size: 36px;
    font-weight: 400;
    color: $dark-gray; }
.rules-link {
    color: $blue-link;
    text-decoration: none;
    &:hover {
        color: #000; } }
.block {
    position: relative;
    &:before {
        content: '';
        display: block;
        width: inherit;
        height: inherit;
        position: relative;
        top: 0;
        left: 0;
        z-index: 1; }
    &:after {
        content: '';
        display: block;
        width: inherit;
        height: inherit;
        position: absolute;
        top: 9px;
        left: 9px; } }

.block-frame {
    &:after {
        border: 3px solid $dark-gray; } }




